
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import request from './ajax.js'

Vue.prototype.$http=request;


 Vue.config.productionTip = false
// // 阻止 vue 在启动时生成生产提示。

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    request,
    components: { App },
    template: '<App/>'
})


