
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import {mapState, mapGetters, mapMutations} from 'vuex'
export default {
name: 'app',
components:{
Tab,
TabItem,
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet,
},
data:function(){
return {}
},
computed:{
...mapState([
'topics',
'loginedMoreMenus',
'unLoginedMoreMenus',
'isLogined',
'isShowMoreMenus'
]),
...mapGetters(['moreMenus']),
isShowMoreMenus:{
get:function(){
return this.$store.state.isShowMoreMenus;
},
set:function(nval){
this.$store.commit('mutationIsShowMoreMenus',nval);
}
}
},

created: function(){

var loginStatus = window.localStorage.getItem('loginStatus');

if(loginStatus){

this.mutationLogin();
this.mutationLoginname(
window.localStorage.getItem('loginname'));
this.mutationAccessToken(
window.localStorage.getItem('accesstoken'));
}
},

methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken']),
routeto(index){






this.$router.push('/topiclist/'+this.topics[index].name);
},
showMoreMenus(){


this.isShowMoreMenus = true
},

handleClickMoreMenu(menukey,menuitem){
console.log(menukey);
this.$router.push('/'+ menukey);
}
}
}



